@import url('./reset.scss');
@import url('https://fonts.googleapis.com/css?family=Quicksand:400,700|Permanent+Marker|Lato:300,400|Montserrat:300,700&display=swap');

* {
  margin: 0;
  padding: 0;
  position: relative;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  width: 100%;
  font-size: 16px;
  font-family: 'Quicksand', 'sans-serif';
  font-weight: 300;
  background: #fff;
  color: #292929;
}

h1 {
  font-family: 'Permanent Marker', 'sans-serif';
  font-size: 4rem;
  margin: 20px 0;
  @media screen and (min-width: 576px) {
    font-size: 7rem;
    letter-spacing: 0.5rem;
    font-style: italic;
  }
}

h2 {
  font-size: 1.2rem;
  margin: 10px 0;
  @media screen and (min-width: 576px) {
    font-size: 1.5rem;
    letter-spacing: 0.05rem;
  }
}

h3 {
  font-size: 1.1rem;
  margin: 10px 0;
  @media screen and (min-width: 576px) {
    font-size: 1.25rem;
  }
}

h4 {
  font-family: 'Lato', 'sans-serif';
  font-weight: 400;
  font-size: 1.1rem;
  margin: 10px 0;
  text-transform: uppercase;
  letter-spacing: 0.1rem;
  margin-bottom: 25px;
  @media screen and (min-width: 576px) {
    font-size: 1.2rem;
  }
}

h1,
h2,
h3,
h4 {
  text-align: center;
  &.headline {
    text-transform: uppercase;
    color: #ff4757;
  }
}

h3.headline,
h4.headline {
  color: inherit;
  font-family: Lato;
  letter-spacing: 0.1rem;
  font-weight: 300;
}

p {
  font-size: 1rem;
  line-height: 1.5rem;
}

span {
  line-height: 1.5rem;
  strong {
    font-weight: 700;
  }
  &.snippet {
    max-width: 576px;
    text-align: center;
  }
}

a {
  text-decoration: none;
  color: #292929;
  transition: all 0.25s ease;
  &:hover {
    color: #ff4757;
    &.button {
      color: #fff;
    }
  }
  &.active {
    color: #ff4757;
  }
}

button,
input[type='submit'],
.button {
  appearance: none;
  font-family: 'Quicksand', 'sans-serif';
  font-size: 0.9rem;
  text-transform: uppercase;
  text-align: center;
  padding: 15px 20px;
  margin: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  letter-spacing: 0.1rem;
  color: #fff;
  border: 1px solid #88b1b2;
  background-color: #88b1b2;
  transition: all 0.25s linear;
  cursor: pointer;
  @media screen and (min-width: 576px) {
    font-size: 1rem;
    padding: 15px 30px;
  }
  &:hover {
    border-color: #2ed573;
    padding: 15px 50px;
    background-color: #2ed573;
  }
  &.round {
    border-radius: 25px;
  }
}
input[type='submit'] {
  &:hover {
    padding: 15px 30px;
  }
}

.menu-button {
  position: fixed;
  top: 10px;
  left: 10px;
  z-index: 1002;
  display: block;
  opacity: 0;
  transition: all 1s ease;
  @media screen and (min-width: 1200px) {
    display: none;
  }
}
nav {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  padding: 25px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  z-index: 1001;
  opacity: 0;
  height: 100vh;
  overflow: auto;
  transition: all 0.5s ease;
  transform: translate(-100%);
  ul {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
    li {
      padding: 25px 0;
    }
  }

  @media screen and (min-width: 576px) {
    width: 300px;
  }
  @media screen and (min-width: 1200px) {
    width: 100vw;
    transform: translate(0);
    flex-direction: row;
    height: unset;
    transition: all 1s ease;
    ul {
      flex-direction: row;
      li {
        padding: 0 10px;
      }
    }
  }
  &.active {
    transform: translate(0);
  }
  .background {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background: #fff;
    box-shadow: 0 0 5px #292929;
  }
  .logo {
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    margin-bottom: 50px;
    @media screen and (min-width: 1200px) {
      margin-bottom: 0;
      flex: 1 0 50%;
    }
    h1 {
      font-size: 1.2rem;
      letter-spacing: 0.1rem;
      margin: 0;
      line-height: 1rem;
      text-align: center;
    }
  }
  ul {
    flex: 1 0 50%;
    max-width: 960px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
  }
  li {
    font-family: 'Montserrat', 'Lato', 'sans-serif';
    text-transform: uppercase;
    font-size: 0.8rem;
    letter-spacing: -0.0125rem;
    padding-right: 50px;
    font-weight: 300;
  }
}

section {
  min-height: 100vh;
  padding: 100px 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  overflow: hidden;
  @media screen and (min-width: 576px) {
    padding: 100px 50px;
  }

  .background {
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    &.filter {
      &::after {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        height: 100%;
        width: 100%;
        background-color: #000;
        opacity: 0.25;
      }
    }
  }

  .container {
    width: 100%;
    max-width: 960px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    .title {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      margin-bottom: 50px;
      &.inline {
        margin: 50px 0 10px 0;
      }
      h2 {
        font-size: 2.5rem;
        @media screen and (min-width: 576px) {
          font-size: 3rem;
        }
      }
    }

    .image {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      align-items: stretch;
      max-width: 960px;
      overflow: hidden;

      .img {
        flex: 1 1 50%;
        min-width: 260px;
        padding: 25px 0;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        &.zoom {
          background: rgba(41, 41, 41, 1);
          position: fixed;
          top: 0;
          left: 0;
          width: 100vw;
          height: 100vh;
          z-index: 1001;
          display: flex;
          justify-content: center;
          align-items: center;
          cursor: zoom-out;
          @media screen and (min-width: 768px) {
            padding: 50px;
          }
        }

        @media screen and (min-width: 576px) {
          padding: 25px 10px;
        }
        img {
          max-width: 100%;
          max-height: 100%;
          cursor: zoom-in;
        }
      }
      .caption {
        margin-top: 10px;
        font-style: italic;
      }
    }

    .cards {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: stretch;
      max-width: 960px;
      padding-bottom: 50px;
      overflow: visible;
      .card {
        width: 260px;
        flex: auto;
        max-width: 320px;
        padding: 25px 0;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        @media screen and (min-width: 576px) {
          padding: 25px 10px;
        }

        &:hover {
          .icon {
            color: #ff4757;
            border-color: #ff4757;
          }
        }

        .icon {
          display: flex;
          justify-content: center;
          align-items: center;
          height: 125px;
          width: 125px;
          margin-bottom: 50px;
          border: 1px solid #cccccc;
          border-radius: 50%;
          transition: all 0.5s ease;
        }

        .content {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          h4,
          p {
            width: 100%;
            text-align: center;
          }
        }
      }
    }

    .table {
      width: 100%;
      max-width: 960px;
      counter-reset: day;
      overflow: hidden;
      table,
      tbody,
      td,
      tr {
        display: block;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        padding: 10px 0;
      }
      td {
        .text {
          padding: 50px 25px;
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          &::after {
            counter-increment: day;
            content: counter(day, decimal-leading-zero);
            position: absolute;
            top: 0;
            left: 0;
            font-size: 5rem;
            opacity: 0.3;
            padding: 10px;
            color: #aaa;
            z-index: -1;
          }
          @media screen and (min-width: 576px) {
            &::after {
              font-size: 8rem;
            }
          }
          &.no-counter {
            &::after {
              counter-increment: unset;
              display: none;
            }
          }
          span {
            &.headline {
              margin: 10px 0;
              font-family: 'Montserrat', 'Lato', 'sans-serif';
              text-transform: uppercase;
              font-size: 1rem;
              letter-spacing: -0.0125rem;
              font-weight: 700;
            }
            &.counter {
              position: absolute;
              top: 3.25rem;
              left: 0;
              font-size: 5rem;
              opacity: 0.3;
              padding: 10px;
              color: #aaa;
              z-index: -1;
              @media screen and (min-width: 576px) {
                font-size: 8rem;
              }
            }
          }
          p {
            text-align: center;
          }
        }
        .img {
          height: 400px;
          background-size: cover;
          background-position: center;
          background-repeat: no-repeat;
          overflow: hidden;
        }
      }
    }

    @media screen and (min-width: 576px) {
      .table {
        table {
          display: grid !important;
          grid-template-columns: repeat(3, 1fr);
          grid-auto-rows: 1fr;
          row-gap: 10px;
        }
        tbody,
        tr {
          display: contents !important;
        }
        td {
          height: 100%;
          .img {
            height: 100% !important;
            width: 100%;
          }
        }
      }
    }

    .videos {
      width: 100%;
      display: flex;
      flex-wrap: wrap;
      justify-content: space-around;
      align-items: stretch;
      max-width: 960px;
      margin: 50px 0;
      overflow: hidden;
      .video {
        flex: auto;
        min-width: 260px;
        padding: 25px 0;
        display: flex;
        flex-direction: column;
        justify-content: flex-start;
        align-items: center;
        @media screen and (min-width: 576px) {
          padding: 25px 10px;
        }

        .video-react {
          outline: 0;
          font-family: 'Quicksand', 'sans-serif';
          .video-react-video {
            outline: 0;
          }
          .video-playButton {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            margin: 0;
            color: #cccccc;
            font-size: 5em;
            height: 1.5em;
            width: 1.5em;
            display: block;
            padding: 0;
            cursor: pointer;
            opacity: 1;
            border-radius: 50%;
            border: 0;
            background: none;
            transition: all 0.4s;
            &:hover {
              color: #fff;
            }
            &.big-play-button-hide {
              opacity: 0;
            }
            &::before {
              top: 50%;
              left: 50%;
              transform: translate(-50%, -50%);
            }
          }
          .video-controlBar {
            background: linear-gradient(
              0,
              rgba(41, 41, 41, 1) 25%,
              rgba(255, 255, 255, 0) 100%
            );
            .video-react-button:hover {
              background-color: inherit;
            }
          }
          .video-react-time-tooltip,
          .video-react-mouse-display:after,
          .video-react-play-progress:after {
            font-family: 'Quicksand', 'sans-serif';
            background: rgba(41, 41, 41, 1);
            color: #fff;
            display: flex;
            padding: 5px;
          }
        }
      }
    }
  }
  &.banner {
    justify-content: center;
    color: #fff;
  }

  &.quick-checkout {
    min-height: unset;
    color: #fff;
    h1 {
      font-size: 3rem;
    }
    h2 {
      font-size: 1.25rem;
      text-transform: uppercase;
      margin-bottom: 25px;
    }
    form {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      width: 100%;
      max-width: 576px;

      @media screen and (min-width: 576px) {
        flex-direction: row;
      }

      input[type='email'] {
        font-family: 'Quicksand', 'sans-serif';
        font-size: 1.2rem;
        padding: 10px 25px;
        height: 50px;
        outline: 0;
        width: 90%;
        max-width: 320px;
        border: 1px solid #fff;
        border-radius: 25px;
        @media screen and (min-width: 576px) {
          flex-direction: row;
          width: 100%;
          border-right: none;
          border-radius: 25px 0 0 25px;
        }
      }
      input[type='submit'] {
        height: 50px;
        border-radius: 25px;
        margin-top: 25px;
        transform: translateX(-5px);
        background: #ff6b81;
        border-color: #ff6b81;

        @media screen and (min-width: 576px) {
          border-left: none;
          border-radius: 0 25px 25px 0;
          margin: 0;
        }

        &:hover {
          background: #ff4757;
          border-color: #ff4757;
        }
      }
    }
    &.alt {
      background: #ff4757;
      input[type='submit'] {
        background: #292929;
        border-color: #292929;
        &:hover {
          background: #000;
          border-color: #000;
        }
      }
    }
  }
}

footer {
  display: flex;
  justify-content: center;
  padding: 25px 0;
  background: #292929;
  color: #fff;
  .container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    max-width: 960px;
    @media screen and (min-width: 576px) {
      flex-direction: row;
      align-items: flex-start;
    }

    .logo {
      h1 {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
        font-size: 2rem;
        letter-spacing: 0.1rem;
        margin: 0;
        color: #fff;
      }
    }
    .menu {
      width: 100%;
      @media screen and (min-width: 576px) {
        width: unset;
      }
      ul {
        display: flex;
        flex-direction: column;
        align-items: center;
        flex-wrap: wrap;
        max-height: 120px;
        margin-top: 25px;
        @media screen and (min-width: 576px) {
          align-items: unset;
          margin: 0;
        }
        a:hover {
          color: #ff4757;
        }
        li {
          color: #fff;
          font-size: 0.9rem;
          padding: 10px 20px;
          transition: all 0.5s ease;
          &:hover {
            color: #ff4757;
          }
        }
      }
    }
    .copywrite {
      .socialmedia {
        display: flex;
        justify-content: center;
        @media screen and (min-width: 576px) {
          justify-content: flex-start;
        }
        .icon {
          margin-right: 10px;
          height: 2rem;
          width: 2rem;
          display: flex;
          justify-content: center;
          align-items: center;
          border-radius: 50%;
          transition: all 0.25s ease;
          color: #fff;
          &:hover {
            background: #ff4757;
          }
        }
      }
      span {
        margin-top: 25px;
        font-size: 0.9rem;
      }
    }
  }
}

.parallax {
  width: 100vw;
  display: flex;
  justify-content: center;
}

.prices {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: 1fr;
  column-gap: 25px;
  margin: 25px 0;
  @media screen and (min-width: 768px) {
    grid-template-columns: 1fr 1fr;
  }

  &.single-col {
    grid-template-columns: 1fr;
    .price_container {
      border-radius: 25px !important;
      border-color: #292929 !important;
      li:nth-of-type(even) {
        color: #fff;
        background-color: #292929 !important;
      }
      .price_price {
        background-color: #292929 !important;
      }
    }
  }

  .price_entry {
    width: 360px;
    height: 100%;
    // width: 100%;
    margin-top: 25px;
    @media screen and (min-width: 576px) {
      margin: 0;
    }
    &:first-of-type {
      margin: 0;
    }
    &:nth-of-type(odd) {
      .price_container {
        border-color: #ff4757;
      }
      .price_price:nth-of-type(even) {
        background-color: #ff6b81;
      }
    }
    &:nth-of-type(even) {
      .price_title {
        background-color: #6bb6b8;
      }
      .price_container {
        border-color: #6bb6b8;
      }
      .price_price:nth-of-type(odd) {
        background-color: #88b1b2;
      }
    }

    .price_title {
      padding: 25px;
      color: #fff;
      background-color: #ff4757;
      border-radius: 25px 25px 0 0;

      &:last-of-type {
        background-color: #6bb6b8;
        margin: 0;
      }
      span {
        display: block;
        width: 100%;
        text-align: center;
        font-family: 'Montserrat', 'sans-serif';
        font-size: 0.6em;
        text-transform: uppercase;
        letter-spacing: 0.1rem;
        &:last-of-type {
          font-family: 'Lato';
          font-size: 1.5rem;
        }
      }
    }
    .price_container {
      width: 100%;
      display: flex;
      flex-direction: column;
      justify-content: flex-start;
      align-items: center;
      border: 2px solid;
      border-radius: 0 0 25px 25px;
      .price_info {
        width: 100%;
        ul {
          display: flex;
          flex-direction: column;
          justify-content: center;
          align-items: center;
          width: 100%;
          li {
            padding: 15px 25px;
            text-align: center;
            width: 100%;
            strong {
              font-weight: 700;
            }
          }
        }
      }
      .price_date {
        width: 100%;
        padding: 25px 0;
        span {
          display: block;
          width: 100%;
          text-align: center;
          font-family: 'Montserrat', 'sans-serif';
          font-size: 0.6em;
          text-transform: uppercase;
          letter-spacing: 0.1rem;
          &:last-of-type {
            font-family: 'Lato';
            font-size: 1.5rem;
          }
        }
      }
      .entry {
        padding: 25px 0;
        background: #292929;
        color: #fff;
        width: 100%;
        display: flex;
        justify-content: center;
      }
      .price_price {
        padding: 25px 25px 15px 25px;
        white-space: nowrap;
        width: 100%;
        color: #fff;
        display: flex;
        flex-flow: column;
        justify-content: center;
        align-items: center;
        &:last-of-type {
          border-radius: 0 0 23px 23px;
        }
        .price_price_container {
          display: flex;
          justify-content: center;
          align-items: flex-start;
          .price_value {
            font-size: 3rem;
            line-height: 3rem;
            letter-spacing: 0.025rem;
            margin-right: -0.025rem;
            text-align: center;
          }
          .price_label {
            margin-right: 5px;
            text-transform: uppercase;
            top: -5px;
          }
        }
        .price_price_snippets {
          display: flex;
          flex-flow: column;
          justify-content: center;
          align-items: center;
          padding-top: 10px;
          .price_pre-snippet,
          .price_snippet {
            font-size: 0.75rem;
            text-transform: uppercase;
          }
        }
        .price_pre-snippet:last-of-type {
          padding-bottom: 10px;
        }
        .price_snippet:first-of-type {
          padding-top: 10px;
        }
      }
    }
  }
}
